* {
  margin: 0;
  padding: 0;
}

:root {
  --lt-grey: #f2f2f2;
  --black: #232323;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.zoom-widget {
  display: flex;
  align-items: center;
}
.zoom-widget p {
  margin-right: 8px;
}
.zoom-widget .zoom-level {
  border: 1px solid var(--lt-grey);
  padding: 4px 8px;
  border-radius: 50%;
  cursor: pointer;
  transition: border 0.25s ease;
}
.zoom-widget .zoom-level:hover {
  border-color: var(--black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
  padding: 32px;
}

.box-shadow, .multi-carousel {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.multi-carousel {
  overflow: hidden;
  width: 400px;
  border: 1px solid var(--lt-grey);
  padding: 16px;
}
.multi-carousel img {
  -o-object-fit: cover;
     object-fit: cover;
}
.multi-carousel .previews {
  display: flex;
}

.preview-image {
  position: relative;
  top: 0px;
  left: 0px;
  background-position: center center;
  width: 200px;
  height: 200px;
  background-size: cover;
  transition: background-size 0.25s ease;
  cursor: pointer;
  margin-bottom: 8px;
  z-index: 0;
}

.carousel-prev {
  height: 80px;
  flex: 1;
  margin-right: 8px;
}
.carousel-prev:last-of-type {
  margin-right: 0;
}
.carousel-prev img {
  width: 100%;
  height: 100%;
  margin-right: 8px;
}
.carousel-prev--not-selected {
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.25s ease;
}
.carousel-prev--not-selected:hover {
  opacity: 1;
}

.carousel-bottom-section {
  background-color: #fff;
  z-index: 2;
}

@keyframes top-to-bottom {
  0% {
    top: -100%;
  }
  100% {
    top: 0px;
  }
}
.top-to-bottom-anim {
  animation: top-to-bottom 0.5s ease;
}

@keyframes bottom-to-top {
  0% {
    top: 100%;
  }
  100% {
    top: 0px;
  }
}
.bottom-to-top-anim {
  animation: bottom-to-top 0.5s ease;
}

@keyframes left-to-right {
  0% {
    left: 0px;
  }
  100% {
    left: 100%;
  }
}
.left-to-right-anim {
  animation: left-to-right 0.5s ease;
}

@keyframes right-to-left {
  0% {
    left: 100%;
  }
  100% {
    left: 0px;
  }
}
.right-to-left-anim {
  animation: right-to-left 0.5s ease;
}/*# sourceMappingURL=styles.css.map */